import React from 'react';
import { Link } from "react-router-dom";
import  "./header.css";

const Header = ({lang, setLanguaje}) =>{
    const handleClick = (bool) => setLanguaje(bool)
    return(
        <nav className="container nav">
            <div className="logo">
                <Link to="/">Grow Market Logo</Link>
            </div>
            <div className="menu">
                <ul className="list">
                    <li>
                        <Link to="/">
                            {lang ? 'Começo' : 'Inicio'}
                        </Link>
                    </li>
                    <li>
                        <Link to="/about">
                            {lang ? 'Nós' : 'Nosotros'}
                        </Link>
                    </li>
                    <li>
                        <Link to="contact">
                            {lang ? 'Contato' : 'Contacto'}
                        </Link>
                    </li>
                </ul>
                <div className="button"> 
                    <div onClick={()=>handleClick(true)}
                        className={lang ? 'buttonActive' : ''}>
                        Pr
                    </div>
                    <div onClick={()=>handleClick(false) }
                        className={!lang ? 'buttonActive' : ''}>
                        Es
                    </div>
                </div>
            </div>
        </nav>
    );
}
export default Header;